import React, {useState, useEffect} from 'react';
import './App.css'
import personService from './services/persons'

const App = () => {
  const [ persons, setPersons] = useState([]) 
  const [ newName, setNewName ] = useState('')
  const [filter, setFilter] = useState('')
  const [newTel, setTel] = useState('')
  const [statusMsg, setStatusMsg] = useState({text:`tekstiÃ¤`,type: ''})
  useEffect(()=>{
    personService.getAll()
    .then(result=>{
      setPersons(result)
      console.log('result',result)
    })
  },[])
 const handleNewPerson = (event) =>{
   event.preventDefault()
   
   if(persons.find(p=>p.name===newName)===undefined){
   const newPerson = {
     //id : persons.length +1, //json file starts from 1, not 0
     name: newName,
     number: newTel
   }
   personService.create(newPerson).then(result=>{
    setPersons(persons.concat(result))
    setStatusMsg({text:`Added ${newName}`,type:'notification'})
    setNewName('')
    setTel('')
    setTimeout(()=>{
        setStatusMsg({text:'',type:''})
    },5000)
   })
  
  }else{
     const person = persons.find(p=>p.name === newName)

     if(person.number!== newTel){
       if(window.confirm(`${person.name} is already added to phonebook, replace old number with new one?`)){
       const changedPerson = {...person, number: newTel }
       personService.update(changedPerson.id,changedPerson)
       .then(result =>{
         setPersons(persons.map(p=> p.id!==changedPerson.id ? p : result))
       }).catch(error => {
         setStatusMsg({text:`Information on ${changedPerson.name} has already been removed from server`,type: `error`})
         setPersons(persons.filter(p=> p.id!==changedPerson.id))
         setTel('')
         setNewName('')
         setTimeout(()=>{
           setStatusMsg({text:``,type:``})
         },3000)
       })
       }
     } else{
       window.alert(`${newName} is alredy on the list with same phone number`)
     }
  }
 }
 const personsFiltered = persons.filter(p => p.name.toLowerCase().includes(filter.toLowerCase()))


 const handleNameChange = (event)=>{
   setNewName(event.target.value)
 }
const handleTelChange = (event) =>{
  setTel(event.target.value)
}
const handleFilter = (event) =>{
  setFilter(event.target.value)
}
const handleRemove=(id)=>{
 
  const person = persons.find(p=>p.id===id)
  if(window.confirm(`Delete ${person.name} ?`)){
  
    personService.remove(person.id).then(result =>{
    
      setPersons(persons.filter(p=> p.id!==id))
    })
  }
}

const rows = () =>{
 return personsFiltered.map(p=> <Person personName={p.name} 
  personTel={p.number} personId = {p.id}
  handleRemove = {()=>handleRemove(p.id)} key={p.id}/>)

}
  return (
    <div>
      <h2><u>Phonebook</u></h2>
      <Notification statusMsg={statusMsg}/>
      <FilterForm filter ={filter} handleFilter ={handleFilter} text='Filter shown with'/>
      <h3>Add new</h3>
      <Addform name ={newName} handleName={handleNameChange} 
                    number = {newTel} handleN ={handleTelChange}
                    formHandler ={handleNewPerson}/>

      <h3>Numbers</h3>
      {rows()}
    </div>
  )

}

const Person = ({personId, personName,personTel,handleRemove}) =>{
return(
 
  <p id={personId}>{personName} {personTel} <button onClick={handleRemove}>Delete</button></p>
  
)

}
const FilterForm=({filter,handleFilter,text})=>{
  return(
    <form>
      {text} <input value={filter} onChange ={handleFilter}/>
    </form>
  )

}
const Notification = ({statusMsg})=>{
if(statusMsg.type===''){
  return null
}else {
return(
  <div className={statusMsg.type}>
     {statusMsg.text}
  </div>
)

}


}


const Addform = ({name,handleName,number,handleN,formHandler})=>{
return(
  <form  onSubmit={formHandler}>
  <div>
    name: <input value ={name} onChange ={handleName}/><br></br>
    number: <input value={number} onChange={handleN}/>
  </div>
  <div>
    <button type="submit">add</button>
  </div>
  </form>
)
}

export default App;
